<template>
    <div class="setComponent">
        <div class="form-item">
            <label></label>
            <el-button size="mini" @click="titleClick('title')"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button type="primary" round size="mini" @click="addClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-button :disabled="obj.imgTextBox.length==0" type="danger" round size="mini" @click="removeClick"></el-button>
        </div>
        <div class="form-item">
            <label>LOGO</label>
            <el-button size="small" type="primary" @click="imgloadClick"></el-button>
        </div>
        <div class="form-item">
            <label></label>
            <el-color-picker v-model="obj.backgroundColor"></el-color-picker>
        </div>
        <div class="form-item">
            <label></label>
            <el-color-picker v-model="obj.textColor"></el-color-picker>
        </div>
        <div class="form-item">
            <label></label>
            <el-color-picker v-model="obj.activeColor"></el-color-picker>
        </div>
        <div class="form-item">
            <label></label>
            <el-input-number v-model="obj.paddingMenuLeft"></el-input-number>   
        </div> 
    </div>
</template>
<script>
export default {
    name: 'SetRow',
    props: {
        obj: {
            type: Object,

        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        imgloadClick() {
            this.$emit('imgloadClick',this.obj)
        },
        addClick() {
            this.obj.imgTextBox.push(JSON.parse(JSON.stringify(this.obj.imgTextBox[0])))
        },
        removeClick() {
            this.obj.imgTextBox.pop()
        },
        titleClick(type) {
            this.$emit('titleClick',[this.obj,type])
        }
    }
}
</script>
<style lang="less" scoped>
  
</style>

